
import Panel from 'vux/src/components/panel/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'

import { mapGetters } from 'vuex'

export default {
    components: {
        Panel,
        Swiper
    },
    created(){
        this.$store.dispatch('get_article_list', this.$route.params.categoryid)
    },
    computed: {
        ...mapGetters([
            'articletext',
            'articleimg'
        ])
    },
    methods: {
        routechanged() {
            this.$store.dispatch('get_article_list', this.$route.params.categoryid)
        },
    },
    watch: {
        // 如果路由有变化，会再次执行该方法
        $route: 'routechanged',
    },
    data() {
        return {

        }
    }
}




